.App {
  height: 90vh;
  height: 878.4px;
  width: 1000px;
  display: flex;
  flex-direction: column;
  /* border: 5px solid red; */
}

.toggle-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.toggle-btn {
  height: 3vh;
  height: 29.28px;
  margin: 1vh 0;
  margin: 9.76px 0;
  border: none;
  color: black;
}

.modeIcons {
  width: 25px;
  margin: 0;
}

.light {
  background: hsl(267, 96%, 91%);
}

.dark {
  background: var(--dark-blue);
}

main {
  padding: 0 7.5%;
  height: 85vh;
  height: 830px;
  position: relative;
  overflow: hidden;
}

main div {
  box-sizing: border-box;
}

.blur {
  position: absolute;
}

.top-left-blue {
  height: 200px;
  width: 100px;
  background: blue;
  top: 0;
  border-radius: 100%;
  left: 0;
  filter: blur(100px);
}

.top-left-red {
  height: 100px;
  width: 60px;
  background: red;
  border-radius: 100%;
  left: 200px;
  filter: blur(75px);
  transform: rotate(-30deg);
}

.top-right-blue {
  height: 125px;
  width: 75px;
  background: blue;
  top: 0;
  border-radius: 100%;
  right: 0;
  filter: blur(100px);
}

.floating-top-middle-red {
  height: 50px;
  width: 50px;
  background: red;
  border-radius: 100%;
  top: 125px;
  left: 450px;
  filter:blur(60px);
}

.middle-right-red {
  height: 100px;
  width: 100px;
  background: red;
  border-radius: 100%;
  right: 0;
  top: 400px;
  filter: blur(80px);
}

.bottom-left-red {
  height: 150px;
  width: 150px;
  background: red;
  border-radius: 100%;
  bottom: 0;
  left: 0;
  filter: blur(130px);
}

.bottom-right-blue {
  height: 200px;
  width: 200px;
  background: blue;
  border-radius: 100%;
  bottom: 0;
  right: 0;
  filter: blur(150px);
}