.hero {
    /* border: 1px solid lightpink; */
    height: 52.5vh;
    height: 512.4px;
    padding-top: 3rem;
    position: relative;
}

.header {
    font-size: 40px;
    line-height: 60px;
}

.nftCircle {
    position: absolute;
    top: 140px;
    width: 110px;
    height: 55px;
    filter: brightness(1.5);
    left: -10px;
}

.white-t-cover {
    width: 15px;
    height: 5px;
    background: white;
    position: absolute;
    top: 151px;
    left: 73px;
}

.subheader {
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    line-height: 25px;
}

.hero-btns-container {
    margin-top: 1.5rem;
    margin-bottom: 2.75rem;
}

.explore-btn,
.learn-more-btn {
    background: linear-gradient(to right, #e5424f, #2155b1);
    border: none;
    color: white;
    font-size: 12px;
    font-weight: 500;
}

.explore-btn {
    padding: 0.75rem 1.4rem;
    margin-right: 0.75rem;
}

.learn-more-btn {
    padding: 0.75rem 0.8rem;
    border: 1px solid;
    border-image: linear-gradient(to right, #e5424f, #2155b1) 10;
    background: var(--dark-blue);
}

.stats,
.bid {
    display: grid;
}

.stats {
    grid-template: auto auto / auto auto auto;
    border-top: 1px solid rgba(255, 255, 255, 0.125);
    width: fit-content;
    grid-gap: 1rem;
}

.start-stat,
.middle-stat,
.end-stat {
    grid-row-start: 2;
}

.start-stat {
    padding-right: 1.5rem;
}

.middle-stat {
    grid-row-start: 2;
    border-left: 1px solid rgba(255, 255, 255, 0.125);
    border-right: 1px solid rgba(255, 255, 255, 0.125);
    padding: 0 1.5rem;
}

.end-stat {
    padding-left: 1.5rem;
}

.statNum {
    font-size: 24px;
    font-weight: 500;
    margin: 0;
}

.statText {
    font-size: 14px;
    margin-bottom: 0;
    margin-top: 10px;
}

.heroImg {
    height: 420px;
    position: absolute;
    top: 3rem;
    right: 0;
}

.bid {
    /* border: 1px solid lightgray; */
    position: absolute;
    right: 0;
    bottom: 63px;
    align-items: center;
    grid-template: auto / auto auto auto;
    gap: 1rem;
    padding: 0.5rem 0.65rem;
    background: linear-gradient(to left, #0d1526, #15264000);
}

.bid p {
    margin: 0;
    font-size: 12px;
}

.bid-start {
    padding-bottom: 4px;
}

.start-bid-btn {
    background: linear-gradient(to right, #e5424f, #2155b1);
    border: none;
    color: white;
    font-size: 12px;
    padding: 8px 10px;
}