.featured {
    /* border: 1px solid lightgreen; */
    height: 25vh;
    height: 244px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.header-cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
}

.featured-header {
    font-size: 34px;
}

.featured-cta {
    font-weight: 500;
    padding: 0.75rem 1rem;
    color: white;
    border: 1px solid;
    border-image: linear-gradient(to right, #e5424f, #2155b1) 10;
    background: var(--dark-blue);
}

.image-gallery {
    display: flex;
    justify-content: space-between;
}

.image-gallery img {
    width: 200px;
}