.navbar {
    border-bottom: 1px solid rgba(255, 255, 255, 0.125);
    /* border: 1px solid yellow; */
    height: 7.5vh;
    height: 73.2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    display: flex;
    align-items: center;
}

.logo img {
    height: 35px;
}

.logo p {
    font-size: 14px;
    font-weight: 500;
    margin-left: 0.25rem;
}

.pages a {
    font-size: 10px;
    margin: 0 1rem;
}

.cta {
    display: flex;
    align-items: center;
}

.search-btn,
.log-in,
.connect-wallet {
    margin-left: 1rem;
    font-size: 10px;
}

.search-btn {
    width: 25px;
    background: none;
    border: none;
    margin: 0 0.75rem;
}

.log-in,
.connect-wallet {
    background: linear-gradient(to right, #e5424f, #2155b1);
    border: none;
    color: white;
    font-weight: 500;
}

.log-in {
    padding: 0.75rem 2.25rem;
    position: relative;
    border: 1px solid;
    border-image: linear-gradient(to right, #e5424f, #2155b1) 10;
    background: var(--dark-blue);
}


